<template>
    <div class="box">
      <div class="top">
           <div class="banxin">
                 <span>{{this.$store.state.about}}</span>
           </div>
      </div>
      <div class="content">
        <p v-for="item,index in arr" :key="index">{{item.content}}</p>
      </div>
    </div>
    <Footer class="footer"></Footer>
</template>

<script>
import axios from "axios"
import {getCurrentInstance} from 'vue'
import Footer from '../../components/footer.vue'
export default {
   data(){
    return{
        arr:null
    }
   },
   components:{
    Footer
   },
   mounted(){
    const data=getCurrentInstance()?.appContext.config.globalProperties.$data
          axios.get(data.about).then(res=>{
            this.arr=res.data
          })
   }
}
</script>

<style lang="less" scoped>

body{
        background: #ffffff;

}
      .box{
        width: 100%;
        .top{
          height: 84px;
          background: url(../../../public/img/news_bg.png);
          .banxin{
            width: 1260px;
            margin: 0 auto;
            span{
              display: block;
              height: 84px;
              width: 190px;
              line-height: 84px;
              font-size: 28px;
              color: white;
              letter-spacing: 3px;
              border-bottom: 3px solid #c4102f;
            }
          }
        }

        .content{
          width: 1260px;
          height: 100%;
 
          margin: 50px auto;
          p{
           margin-bottom: 40px;
           width: 900px;
           color: #626262;
           font-size: 15px;
           
          }
        }
      }
      .footer{
        position: fixed;
        bottom: 0;
      }
</style>