<template>
  <main>
    <div class="top">
      <div class="title">
        <h1>{{ car }}</h1>
        <span>{{ warn }}</span>
      </div>
      <div class="serach">
        <input type="text" placeholder="请输入搜索内容" />
        <button>搜索</button>
      </div>
    </div>
  </main>

  <section>
    <div class="content">
      <div class="list">
        <div class="choose"><input type="checkbox" checked="true" /> 全选</div>
        <div class="goods">
          <!-- <input type="checkbox"> -->
          <span class="name">{{ goods[0] }}</span>
          <span class="price">{{ goods[1] }}</span>
          <span class="count">{{ goods[2] }}</span>
          <span class="subtotal">{{ goods[3] }}</span>
          <span class="del">{{ goods[4] }}</span>
        </div>
      </div>
      <!-- 商品列表 -->
      <div class="lists" v-for="(item, index) in arr" :key="index">
        <input type="checkbox" checked="true" class="one" />
        <img :src="item.img" alt="" />
        <span class="name">{{ item.title }}</span>
        <span class="price">{{ item.price }}元</span>
        <div class="count">
          <button @click="reduce(index, item.num)">-</button>
          <div>{{ item.num }}</div>
          <button @click="add(index, item.num)">+</button>
        </div>
        <span class="subtota"
          >{{ Math.round(item.price * item.num * 100) / 100 }}元</span
        >
        <span class="del" :style="{ color: 'red' }" @click="del(index)">✖</span>
      </div>
    </div>
  </section>

  <div class="nums">
    <div class="money">
      <div class="stats">
        <p>
          <span>{{ goods[5] }}</span
          >{{ all }}元
        </p>
      </div>
      <button @click="shop">
        {{ goods[6] }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      car: "我的购物车",
      warn: "温馨提示：产品未购买成功，以下为产品清单，请尽快结算",
      goods: ["商品名称", "单价", "数量", "小计", "操作", "合计：", "去结算"],
    };
  },
  methods: {
    //删除商品
    del(index) {
      this.$store.dispatch("delArr1", index);
    },
    //商品数量改变
    reduce(index, num) {
      this.$store.dispatch("floor1", { index: index, num: num - 1 });
    },
    add(index, num) {
      this.$store.dispatch("upArr1", { index: index, num: num + 1 });
    },
    shop(){
      alert('爱情经不起等待，叫我宝贝趁现在')
    }
  },
  computed: {
    //获取state里的arr数据
    ...mapState(["arr"]),

    //计算总价
    all() {
      let nums = this.arr.map((item, index) => {
        return item.price * item.num;
      })
      if (isNaN(nums)||Number(nums)>0) {
        //eval将nums每一项拼接加
        return Math.round(eval(nums.join("+")) * 100) / 100;
      } else {
        return 0;
      }
    },
  },
};
</script>

<style lang="less" scoped>
main {
  width: 100%;
  height: 100px;
  background: white;
  border-bottom: 3px solid gold;
  .top {
    width: 1240px;
    margin: 0 auto;
    // height: 100px;
    position: relative;

    .title {
      h1 {
        line-height: 100px;
        display: inline-block;
        font-weight: 500;
      }
      span {
        margin-left: 20px;
        font-size: 14px;
        color: #999;
      }
    }
    .serach {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      input {
        border: none;
        height: 25px;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
        text-indent: 1em;
        border: 1px solid #999;
        outline: none;
      }
      button {
        height: 25px;
        width: 50px;
        border: none;
        color: white;
        border: 1px solid #f03726;
        background: #f03726;
      }
    }
  }
}

section {
  width: 100%;
  .content {
    width: 1240px;
    margin: 0 auto;
    .list,
    .lists {
      position: relative;
      background: white;
      height: 80px;
      line-height: 80px;
      border-radius: 10px;
      margin-top: 20px;
      .one {
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
      .choose {
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
      .name {
        position: absolute;
        left: 20%;
        top: 50%;
        transform: translate(-20%, -50%);
      }
      .price {
        position: absolute;
        left: 55%;
        top: 50%;
        transform: translate(-60%, -50%);
      }
      .count {
        position: absolute;
        left: 70%;
        top: 50%;
        transform: translate(-70%, -50%);
      }
      .subtotal {
        position: absolute;
        left: 85%;
        top: 50%;
        transform: translate(-85%, -50%);
      }
      .del {
        position: absolute;
        left: 95%;
        top: 50%;
        transform: translate(-95%, -50%);
        cursor: pointer;
      }
    }
    .lists {
      height: 100px;
      margin-top: 10px;
      img {
        width: 100px;
        height: 80px;
        position: absolute;
        left: 7%;
        top: 50%;
        transform: translate(-7%, -50%);
      }
      .count {
        border: 1px solid #999;
        button {
          float: left;
          width: 30px;
          height: 20px;
          border: none;
          cursor: pointer;
        }
        div {
          float: left;
          width: 30px;
          height: 20px;
          line-height: 20px;
          text-align: center;
        }
      }
    }
    .subtota {
      position: absolute;
      left: 85%;
      top: 50%;
      transform: translate(-85%, -50%);
      color: rgb(255, 193, 112);
    }
  }
}

.nums {
  width: 100%;
  height: 50px;
  .money {
    margin: 15px auto;
    width: 1240px;
    height: 50px;
    background: white;
    position: relative;
    .stats {
      position: absolute;
      left: 85%;
      top: 50%;
      width: 190px;
      overflow: hidden;
      transform: translate(-85%, -50%);
      p {
        font-size: 25px;
        color: rgb(255, 188, 136);
        span {
          font-size: 12px;
        }
      }
    }
    button {
      height: 50px;
      width: 150px;
      border: none;
      border: 1px solid rgb(230, 155, 101);
      position: absolute;
      right: 0;
      color: white;
      background: rgb(254, 102, 1);
    }
  }
}
</style>