<template>
  <main>
   <a href="javaScript:;" class="tab" v-for="item,index in arr" :key="index" :style="{'background':`url(${item.img})`,'background-size':'cover'}" >
           <!-- <h3>{{item.title}}</h3>
           <p>{{item.span}}</p>
           <button>{{item.contact}}</button> -->
   </a>
  </main>
</template>

<script>
export default {
data(){
    return{
        arr:[]
    }
},
mounted(){
    fetch("tab.json").then(res=>res.json())
    .then(data=>{
         this.arr=data;
    })
}
}
</script>

<style lang="less" scoped>
main{
    box-sizing: border-box;
    width: 1260px;
    height: 210px;
    margin: 0 auto;
    // background: red;
    display: flex;
    justify-content: space-around;
    // background-size: cover;
    padding: 40px 0;
    .tab{
        display: block;
        width: 300px;
        height: 130px;
        &:hover{
            opacity: 0.6;
        }
        
    }
}
</style>>

