<template>

<main>
  <div class="banner">
 <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src="img/banner1.jpg" /></div>
        <div class="swiper-slide"><img src="img/banner2.jpg" /></div>
        <div class="swiper-slide"><img src="img/banner3.jpg" /></div>
      </div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-pagination"></div>
      <!-- Add Scrollbar -->
      <!-- <div class="swiper-scrollbar"></div> -->
    </div>
  </div>
  </main>

  
</template>
<!-- 
1 安装 cnpm i swiper@4 -S
1  导包  main.js  import "swiper/dist/css/swiper.min.css"
2  导包 使用轮播图的组件里  import Swiper from "swiper"
3   三层div 复制过来  swiper-container swiper-wrapper swiper-slide
4 是否需要焦点  左右切换  请到官网查料
5  js mounted方法里
 -->
<script>
import Swiper from "swiper";

export default {
  mounted() {
    new Swiper(".swiper-container", {
      slidesPerView: 1.0,
      centeredSlides: true,
      spaceBetween: 30,
      autoplay: true, //可选选项，自动滑动
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  width: 100%;
  height: 100%;
}
.swiper-container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}
main {
  width: 1900px;
  height: 850px;
  position: relative;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 90px;
}

</style>
