import { createStore } from 'vuex'

export default createStore({
  state: {
    arr: [],
    test:'新游测试',
    about:'网易游戏简介'
  },
  getters: {
  },
  mutations: {
    addArr0(state, el) {
      state.arr.unshift(el);
      // 将对象转为json字符串
      localStorage.arr = JSON.stringify(state.arr)
    },
    delArr0(state, index) {
      // 传入的是一个对象，拿到他的索引，在arr里删除
      state.arr.splice(index, 1)
      localStorage.arr = JSON.stringify(state.arr)
    },

    //数量num更新
    floor0(state, obj) {
      if(obj.num<1){
        obj.num=1
      }else{
        state.arr[obj.index].num = obj.num;
      }
    
      localStorage.arr = JSON.stringify(state.arr)
    },
    upArr0(state, obj) {
      state.arr[obj.index].num = obj.num;
      localStorage.arr = JSON.stringify(state.arr)
    }
  },
  actions: {
    addArr1({ commit }, el) {
      commit('addArr0', el)
    },
    delArr1({ commit }, index) {
      commit('delArr0', index)
    },
    floor1({ commit }, obj) {
      commit('floor0', obj)
    },
    upArr1({ commit }, obj) {
      commit('floor0', obj)
    },
  },
  modules: {
  }
})
