import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Shopping from '../views/Shopping.vue'
import About from '../views/title/about.vue'
import Panner from '../views/title/panner.vue'
import Plus from '../views/title/plus.vue'
import YYDS from '../views/title/yyds.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },

  {
    path: '/about',
    name: 'about',
    component: About
  },
  {
    path: '/plus',
    name: 'plus',
    component: Plus
  },
  {
    path: '/panner',
    name: 'panner',
    component: Panner
  },
  {
    path: '/yyds',
    name: 'yyds',
    component: YYDS
  },
  {
    path:'/yyds/shoping',
    name:'shoping',
    component:Shopping
     }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
