<template>
  <main>
    <div class="shop" v-if="list">
      <div class="top">
        <h2>{{ list[0].txt }}</h2>
        <button v-for="(item, index) in types" :key="index" @click="ck(item,index)" :class="{active:ind==index}" ref="bt">
          {{ item }}
        </button>
        <router-link to="/yyds/shoping">{{ car }}</router-link>
      </div>

      <div class="goods">
        <ul>
          <li v-for="(item, index) in List">
            <a href="javaScript:;" :style="{ background: `url(${item.bg})` }">
              <img :src="item.img" alt="" />
            </a>
            <span class="text">
              {{ item.title }}
            </span>
            <span class="price"> ￥{{ item.price }} </span>
            <div class="btn">
              <button @click="addC(item)">{{ shop }}</button>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </main>
  <router-view></router-view>
</template>

<script>
import axios from "axios";
import { getCurrentInstance } from "vue";
export default {
  data() {
    return {
      list: null,
      shop: "加入购物车",
      type: "CS:GO",
      ind: 0,
      car: "购物车",
      arr: [],
    };
  },

  methods: {
    ck(item,index) {
      // 将当前点击对象的值赋给type
      this.type = item;
      this.ind=index
      this.$refs.bt.style.color='red'
    },
    //添加购物车
    addC(item) {
      if (!sessionStorage.getItem(item.title)) {
        item.num = 1;
        //将当前信息存储进浏览器
        sessionStorage.setItem(item.title,JSON.stringify(item))
        this.$store.dispatch("addArr1", item);
        //  alert('商品已添加,请进入购物车查看')
      } else {
        let str =  JSON.parse(sessionStorage.getItem(item.title))
        item.num=str.num
        item.num++
        sessionStorage.setItem(item.title,JSON.stringify(item))
        // alert("进不去")
      }

        //    item.num = 1;
        // //将当前信息存储进浏览器
        // sessionStorage.item = JSON.stringify(item);
        // this.$store.dispatch("addArr1", item);
        //  alert('商品已添加,请进入购物车查看')
    },
  },
  computed: {
    //  数组去重复
    types() {
      let arr = [];
      this.list.forEach((item, index) => {
        // arr里面找不到返回-1
        if (arr.indexOf(item.type) < 0) {
          arr.push(item.type);
        }
      });
      console.log(arr);
      return arr;
    },

    //确定list里面的数据，过滤其他不是同一个类型的数据
    List() {
      let arr = this.list.filter((item) => {
        return item.type == this.type;
      });
      return arr;
    },
  },
  mounted() {
    const data = getCurrentInstance()?.appContext.config.globalProperties.$data;
    axios.get(data.car).then((res) => {
      this.list = res.data;
    });
  },
};
</script>

<style lang="less" scoped>

main {
  width: 100%;
  background: rgb(204, 206, 205);
}
.shop {
  width: 1142px;
  margin: 0 auto 0;
  .top {
    height: 38px;
    border-bottom: 1px solid #bf9e4b;
    position: relative;
    h2 {
      display: inline-block;
      height: 38px;
      width: 120px;
      line-height: 38px;
      font-size: 20px;
      color: white;
      padding: 0 20px;
      background: #bf9e4b;
      background-image: linear-gradient(
        270deg,
        rgba(206, 170, 52, 0.8) 0,
        rgba(189, 149, 42, 0.8) 36%,
        rgba(160, 113, 24, 0.8) 100%
      );
    }
    
.active{
  color: #eea20e;
        border-bottom: 3px solid rgb(243, 219, 80);
}
    button {
      // display: inline-block;
      border: none;
      font-size: 14px;
      margin-left: 20px;
      width: 65px;
      height: 38px;
      background: none;
      cursor: pointer;
      color: #929394;

      // &:hover {
      //   color: #eea20e;
      //   border-bottom: 3px solid rgb(243, 219, 80);
      // }
    }
    a {
      position: absolute;
      right: 10%;
      top: 50%;
      width: 80px;
      height: 38px;
      line-height: 38px;
      background: #263859;
      text-align: center;
      // border-radius: 20px;
      transform: translate(-10%, -50%);
      text-decoration: none;
      color: white;
      font-weight: 600;
      // border: 1px solid red;
      letter-spacing: 2px;
    }
  }
}
.goods {
  ul {
    li {
      width: 100%;
      list-style: none;
      margin-top: 10px;
      height: 140px;
      position: relative;
      background: rgba(235, 237, 236, 1);
      border-radius: 5px;
      a {
        display: inline-block;
        width: 210px;
        height: 140px;
        text-align: center;

        img {
          width: 140px;
          height: 100%;
        }
      }
      .text {
        position: absolute;
        left: 30%;
        top: 50%;
        transform: translate(-30%, -50%);
      }
      .price {
        position: absolute;
        left: 80%;
        top: 50%;
        transform: translate(-80%, -50%);
        color: #eea20e;
      }
      .btn {
        position: absolute;
        right: 5%;
        top: 50%;
        transform: translate(-5%, -50%);
        width: 100px;
        height: 40px;
        // border: 1px solid #999;

        text-align: center;
        button {
          width: 100%;
          height: 100%;
          border-top-left-radius: 35%;
          border-bottom-right-radius: 35%;
          border-color: #c3cf16;
          background: #eb2222;
          color: white;
          letter-spacing: 1px;
        }
      }
    }
  }
}
</style>

